body {
  width: 100%;
  max-width: 1300px;
  margin: 0 auto;
  font-family: "Montserrat";
  font-size: 12px;
  color: #60606E;
}

.Header {
  position: fixed;
  width: inherit;
  max-width: inherit;
  display: flex;
  height: 60px;
  background-color: #DCDCDC;
}
.Header .BurgerNav {
  position: relative;
}
.Header .BurgerNav .hidden-menu {
  position: fixed;
  list-style: none;
  padding: 10px;
  margin: 0;
  box-sizing: border-box;
  width: 200px;
  background-color: red;
  height: 100%;
  top: 0;
  right: -200px;
  transition: right 0.2s;
  z-index: 2;
  -webkit-transform: translateZ(0);
  -webkit-backface-visibility: hidden;
}
.Header .BurgerNav .hidden-menu-ticker {
  display: none;
}
.Header .BurgerNav .btn-menu {
  color: #fff;
  background-color: #666;
  padding: 5px;
  display: block;
  top: 5px;
  right: 0px;
  cursor: pointer;
  transition: right 0.23s;
  z-index: 3;
  width: 25px;
  -webkit-transform: translateZ(0);
  -webkit-backface-visibility: hidden;
}
.Header .BurgerNav .btn-menu span {
  display: block;
  height: 5px;
  background-color: #fff;
  margin: 5px 0 0;
  transition: all 0.1s linear 0.23s;
  position: relative;
}
.Header .BurgerNav .btn-menu span.first {
  margin-top: 0;
}
.Header .BurgerNav .hidden-menu-ticker:checked ~ .btn-menu {
  right: 0px;
}
.Header .BurgerNav .hidden-menu-ticker:checked ~ .hidden-menu {
  right: 0;
}
.Header .BurgerNav .hidden-menu-ticker:checked ~ .btn-menu span.first {
  -webkit-transform: rotate(45deg);
  top: 10px;
}
.Header .BurgerNav .hidden-menu-ticker:checked ~ .btn-menu span.second {
  opacity: 0;
}
.Header .BurgerNav .hidden-menu-ticker:checked ~ .btn-menu span.third {
  -webkit-transform: rotate(-45deg);
  top: -10px;
}

.Main_TopContainer {
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-transform: uppercase;
  background-image: url("../img/top@1X.jpg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
}
.Main_TopContainer_Title {
  padding-top: 75px;
  padding-left: 66px;
  font-size: 34px;
  letter-spacing: 0.04rem;
}
.Main_CenterContainer {
  display: flex;
  margin: 150px 18px;
}
.Main_CenterContainer .Posts {
  margin: 0 62px 50px;
}
.Main_CenterContainer .Posts .Post {
  margin-bottom: 103px;
  max-width: 752px;
}
.Main_CenterContainer .Posts .Post_ImageContainer {
  margin-bottom: 25px;
}
.Main_CenterContainer .Posts .Post_ImageContainer_Image {
  max-width: 752px;
}
.Main_CenterContainer .Posts .Post_InfoContainer {
  display: flex;
  margin-bottom: 15px;
}
.Main_CenterContainer .Posts .Post_InfoContainer_Date {
  padding: 7px 18px;
  font-size: 14px;
  font-weight: bold;
  text-transform: uppercase;
  background-color: #7BEEC7;
  color: #FFFFFF;
}
.Main_CenterContainer .Posts .Post_InfoContainer_Icons {
  display: flex;
}
.Main_CenterContainer .Posts .Post_InfoContainer_Icons_Content {
  margin: 7px 12px;
  font-size: 16px;
}
.Main_CenterContainer .Posts .Post_InfoContainer_Icons_Content .fa {
  font-size: 16px;
  color: #CDCDCD;
  cursor: pointer;
  user-select: none;
}
.Main_CenterContainer .Posts .Post_InfoContainer_Icons_Content .fa-active {
  color: #E66A62;
}
.Main_CenterContainer .Posts .Post_TextContainer_Title {
  margin-bottom: 15px;
  font-size: 18px;
  font-weight: bold;
  letter-spacing: -0.02rem;
  color: #60606E;
}
.Main_CenterContainer .Posts .Post_TextContainer_SubTitle {
  font-size: 14px;
  font-family: "Open Sans";
  line-height: 22px;
  color: #999999;
  margin-bottom: 25px;
}
.Main_CenterContainer .Posts .Post_TextContainer_ContinueTitle {
  text-transform: uppercase;
  text-decoration: none;
  font-size: 14px;
  font-weight: bold;
  color: #60606E;
}
.Main_CenterContainer .SideBar {
  margin: 0 62px;
  width: 265px;
}